

.trainings-section {
    padding: 40px 20px;
    background-color: #ffffff;
    color: #484444;
    text-align: center;
   

  }
  
  .container {
    max-width: 1000px;
    margin: 0 auto;
    
  }
  
  .title {
    font-size: 28px;
    color: #484444;
    margin-bottom: 40px;
  
  }
 
  .trainings-list {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
  
  .training-card {
    background-color: #ebe8e8;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .training-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  }
  
  .training-name {
    font-size: 1.25rem;
    font-weight: bold;
    color: #484444;
    margin-bottom: 10px;
  }
  
  .training-time {
    font-size: 0.9rem;
    color: #484444;
    margin-bottom: 8px;
  }
  
  .training-company {
    font-size: 0.85rem;
    color: #60A5FA;
    font-weight: bold;
  }
  
  @media (min-width: 768px) {
    .section-title {
      font-size: 2.5rem;
    }
  }
  