.about-section {
 /* background-image:url("https://i.pinimg.com/736x/d6/2f/76/d62f762d08ecb0456e9f46967ba34230.jpg"); */
 background-color: #ffffff;
  color: #484444;
  padding: 90px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container2 {
  max-width: 1200px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  align-items: center;
}

.text-container-about {
  text-align: left;
  
}

.titleAbout {
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 20px;
}

.description {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #484444; 
  margin-bottom: 30px;
}

.button-group {
  display: flex;
  gap: 15px;
}

.btn {
  display: inline-block;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1rem;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.btn-primary {
  background-color:  #60A5FA; 
  color: #fff;
}

.btn-primary:hover {
  background-color: #60A5FA; 
  transform: translateY(-3px);
}

.btn-secondary {
  background-color: #484444; 
  color: #fff;
}

.btn-secondary:hover {
  background-color: #374151; 
  transform: translateY(-3px);
}

.image-container {
  text-align: center;
}

.image {
  width: 100%;
  max-width: 400px;
  object-fit: contain;
}
