.contact-section {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 40px;
    padding: 50px 20px;
    background-color: #ffffff;
    color: #484444;
    text-align: left;
  }
  
  .containerContact {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 1000px;
    width: 100%;
    
  }
  .contact-form{
    background-color: #ebe8e8;
    
  }
  .info-box {
    flex: 1;
    max-width: 30%;
    padding: 20px;
    background: #ebe8e8;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  }
  
  .info-box .title {
    font-size: 1.2rem;
    margin-bottom: 10px;
    color: #60A5FA;
    text-transform: uppercase;
    text-align: left; 
  }
  
  .info-box p,
  .info-box a {
    margin: 5px 0 20px 0; 
    font-size: 1rem;
    color: #484444;
    line-height: 1.5;
  }
  
  .info-box a:hover {
    color: #60A5FA;
    text-decoration: underline;
  }
  
  .contact-form {
    flex: 1;
    max-width: 50%;
    background: #ebe8e8;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.2);
  }
  
  .contact-form .form-title {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #60A5FA;
    text-align: left;
  }
  
  .contact-form .form-description {
    margin-bottom: 20px;
    color: #484444;
    text-align: left;
  }
  
  .form-group {
    margin-bottom: 20px;
    display: flex;
    align-items: center; 
    gap: 15px; 
  }
  
  .form-group label {
    font-size: 0.9rem;
    color: #484444;
    margin-bottom: 0; 
    flex: 0 0 100px; 
    text-align: left; 
   
  }
  
  .form-group input,
  .form-group textarea {
    flex: 1; 
    padding: 15px 10px;
    font-size: 1rem;
    border-radius: 4px;
    border: 1px solid #444;
    background: #f3eeee;
    color: black;
  }
  
  .form-group input:focus,
  .form-group textarea:focus {
    outline: none;
    border-color: #60A5FA;
  }
  
 
  .submit-btn {
    background: #60A5FA;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.3s;
    display: block;
    width: 100%;
  }
  
  .submit-btn:hover {
    background: #60A5FA;
  }
  

  textarea {
    resize: none; 
    overflow: auto; 
    border-radius: 5px; 
    background-color: #e1dcdc; 
    color: #ffffff; 
    border: 1px solid #d2cccc; 
    padding: 10px;
  }