
.skills-section {
  background-color: #ffffff; 
  color: #000000; 
  padding: 50px 0;
  text-align: center;
}

.containerSkills {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 20px;
}

.header .icon {
  width: 40px;
  height: 40px;
  color: #60A5FA; 
  margin: 0 auto 20px;
}

.title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #000000; 
}

.description {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 40px;
  color: #484444; 
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.skill-card {
  background-color: #ebe8e8; 
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
}

.skill-content {
  display: flex;
  align-items: center;
  gap: 10px;
}

.skill-icon {
  width: 24px;
  height: 24px;
  color: #60A5FA; 
}

.skill-text {
  font-size: 16px;
  font-weight: 500;
  color: #484444; 
}
