.projects-section {
  background-color: #ffffff; 
  color: #000000; 
  padding: 80px 0;
  text-align: center;
}

.containerProject {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 20px;
}

.header .icon {
  width: 40px;
  height: 40px;
  color: #60A5FA ;
  margin: 0 auto 20px;
}

.title {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #484444;
}

.description {
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 40px;
  color: #484444;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.project-card {
  background-color: #ebe8e8; 
  border-radius: 8px;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3); 
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.5);
}

.project-wrapper {
  display: flex;
  flex-direction: column;
}

.project-image {
  width: 100%;
  height: 240px;
  object-fit: cover;
}

.project-details {
  padding: 20px;
  text-align: left;
}

.project-subtitle {
  font-size: 14px;
  font-weight: bold;
  color: #60A5FA;
  margin-bottom: 10px;
}

.project-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #484444; 
}

.project-description {
  font-size: 14px;
  color: #484444; 
  line-height: 1.6;
}
